.bad2good {
	position: relative;
	padding: 10px 10% 50px 6%;
}
.bad2good .header {
	color: #555;
	font-family: sans-serif;
}
.bad2good .header .box {
	display: inline-block;
	text-align: left;
}
.bad2good .header .box br {
	display: none;
}
.bad2good .header .box a {
	color: #f767f2;
	padding: 0 3px;
}
.bad2good .compare {
	display: flex;
	justify-content: center;
}
.bad2good .compare .code-area {
	width: 45%;
	min-height: 100px;
	max-height: 500px;
	overflow-y: scroll;
	border: 1px solid #eee;
	border-radius: 10px;
	padding: 10px 20px;
	line-height: 1.45;
	font-size: small;
	background-color: #f6f8fa;
}
.bad2good .compare .bad {
	padding-right: 20px;
	background-color: #ffeaef;
}
.bad2good .compare .good {
	padding-left: 20px;
	background-color: #dff1f0;
}
.bad2good .compare .arrow {
	position: relative;
	width: 150px;
	display: flex;
	align-items: center;
}
.bad2good .compare .arrow div {
	position: relative;
	width: 100%;
	top: -10%;
	display: flex;
	justify-content: center;
	justify-content: center;
}
.bad2good .compare .arrow div small {
	background-color: #e9e9e9;
	z-index: 2;
	border-radius: 30px;
	padding: 0px 20px;
	line-height: 24px;
	height: 24px;
	color: #333 !important;
	font-size: 0.875em;
	cursor: pointer;
}
.bad2good .compare .arrow div small:hover {
	background-color: #e0e0e0;
}
.bad2good .compare .arrow div:after {
	background-color: #dedede;
	content: '';
	position: absolute;
	height: 2px;
	width: 100%;
	top: 11px;
	z-index: 1;
}
.bad2good .text {
	width: 60%;
	margin: 0 auto;
	padding: 30px 0;
	color: #555;
}
.bad2good .change {
	text-align: center;
}
.bad2good .change button {
	position: relative;
	overflow: hidden;
	width: 40%;
	height: 50px;
	font-size: 18px;
	padding: 6px 10px;
	color: #fff;
	font-weight: bold;
	border-radius: 0 15px 15px 15px;
	border: 1px solid #e0e0e0;
	background-color: #92cac5;
}
.bad2good .change button:hover {
	cursor: pointer;
	background-color: #81bdb8;
}

.progress-button .progress {
	position: absolute;
	left: 0;
	background: rgba(0, 0, 0, 0.2);
	top: 0;
	width: 0%;
	opacity: 0;
	height: 100%;
	z-index: 0;
	transition: width 0s 0.3s, opacity 0.3s;
}

.progress-button.active .progress {
	opacity: 1;
	width: 100%;
	transition: width 0.8s;
}
